.products {
  border-radius: 7px;
  padding-bottom: 35px;

  .products_box {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
    width: 100%;
    gap: 25px;
    .product_heading {
      width: 100%;
      h3 {
        text-align: left;
        font-size: 24px;
        color: #a8741a;
        margin-bottom: 25px;
      }
    }
  }
}

.items {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 0px 15px;
  flex-wrap: wrap;
  margin-left: -12px;
  width: calc(100% + 12px);
}

.light {
  .products {
    .product_heading {
      h3 {
        color: black;
      }
    }
  }
}
.mobile_filter {
  display: none;
}

@media only screen and (max-width: 1292px) {
  .items {
    .product_item {
      width: calc(100% / 4 - 20px);
    }
  }
}

@media only screen and (max-width: 1100px) {
  .items {
    .product_item {
      width: 270px;
    }
  }
}

@media only screen and (max-width: 891px) {
  .products {
    .items {
      margin-top: 5px;
      .product_item {
        width: calc(100% / 3 - 20px);
      }
    }
  }
}

@media only screen and (max-width: 835px) {
  .products {
    .items {
      justify-content: space-between;
      .product_item {
        max-width: none;
        width: calc(100% / 2 - 20px);
        img {
          height: 230px;
        }
      }
    }
  }
}

@media only screen and (max-width: 536px) {
  .products {
    .product_heading {
      margin-left: 12px;
    }
    .products_box {
      .items {
        width: 100%;
        margin-left: 0;
        justify-content: center;
        gap: 10px;
        .product_item {
          width: 100%;
          img {
            height: 250px;
          }
        }
      }
    }
  }
}
