.product_details_page {
  .product_cover {
    position: relative;
    width: 400px;
    height: 400px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 5px;
    }
    .product_actions {
      position: absolute;
      top: 10px;
      right: 10px;
      width: auto;
    }
  }
  .product_details {
    display: flex;
    gap: 25px;
    flex-wrap: wrap;
    .product_title {
      h4 {
        color: #a8741a;
      }
    }

    .product_description {
      align-items: flex-start;
      width: 55%;
      .price {
        margin-top: 20px;
        p {
          font-size: 22px;
        }
      }

      .description {
        margin-top: 15px;
        color: #ccc;
        font-size: 18px;
      }
      h4 {
        margin-top: 15px;
        font-size: 20px;
      }
    }
  }
}

.light {
  .product_details_page {
    h4 {
      color: #333;
    }
    .product_description {
      .product_title {
        h4 {
          color: #333;
        }
      }
      .description {
        color: #2e2e2e;
      }
    }
    .price {
      p {
        color: black;
        font-weight: bold;
      }
    }
  }
}
